.ui {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.mouth,
.attack,
.fullscreen {
  position: absolute;
  bottom: 30px;
  left: 50px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 3px darkturquoise solid;
  transition: all 0.3s ease;

  &:hover {
    transform: scale(1.1);
    box-shadow: 0 5px 5px darkturquoise;
  }
}


.attack {
  left: 150px;
}

.fullscreen {
  left: 250px;
}
